import Swiper from 'swiper/bundle';

const jsSliderAppThumbs = new Swiper('.jsSliderAppThumbs .slider__inner', {
    speed: 750,
    direction: 'vertical',
    observer: true,
	slidesPerView: 'auto',
    spaceBetween: 0,
    autoHeight: true,
});

const jsSliderApp = new Swiper('.jsSliderApp .slider__inner', {
	slidesPerView: 1,
    speed: 750,
    mousewheel: true,
    observer: true,
    
    effect: 'fade',
    fadeEffect: {
        crossFade: true
    },

    navigation: {
        nextEl: '.jsSliderApp .swiper-button-next',
        prevEl: '.jsSliderApp .swiper-button-prev',
    },

    thumbs: {
        swiper: jsSliderAppThumbs,
    }
});